import React, { useEffect, useState } from 'react';
import axios from 'axios';
import Header from '../../components/Header';

const DashboardInstructorAssign = () => {
  const [students, setStudents] = useState([]);
  const [supervisors, setSupervisors] = useState([]);

  useEffect(() => {
    fetchStudents();
    fetchSupervisors();
  }, []);

  const fetchStudents = async () => {
    try {
      const res = await axios.get('http://localhost:5000/api/instructor/confirmed-students');
      setStudents(res.data);
    } catch (err) {
      console.error(" :", err);
    }
  };

  const fetchSupervisors = async () => {
    try {
      const res = await axios.get('http://localhost:5000/api/instructor/all');
      setSupervisors(res.data);
    } catch (err) {
      console.error("  supervisor :", err);
    }
  };

  const handleSelectInstructor = async (studentId, instructorId) => {
    try {
      await axios.post('http://localhost:5000/api/instructor/assign-instructor', {
        student_id: studentId,
        instructor_id: instructorId,
      });
      console.log("  supervisor ");
      fetchStudents();
    } catch (err) {
      console.error("  supervisor :", err);
    }
  };

  return (
    <div className="min-h-screen bg-[#9AE5F2] font-sans">
      <Header />

      <div className="px-[5%] py-[2%]">
        <h2 className="text-2xl font-extrabold text-[#130347] mb-6">
          
        </h2>

        {students.length === 0 ? (
          <p className="text-[#465d71]"></p>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            {students.map((student) => (
          <div
            key={student.student_id}
            className="relative bg-white text-[#130347] px-[5%] py-[4%] rounded-2xl shadow-md flex flex-row items-start gap-6"
          >
            {/*  */}
            <div className="absolute top-3 right-3 w-[45%] md:w-[40%]">
              <select
                className="p-2 border rounded-lg w-full bg-white shadow-sm focus:ring-2 focus:ring-[#4691D3]"
                value={student.supervisor_id || ''}
                onChange={(e) =>
                  handleSelectInstructor(student.student_id, e.target.value)
                }
              >
                <option value="" disabled hidden></option>
                {supervisors.map((sup) => (
                  <option key={sup.supervisor_id} value={sup.supervisor_id}>
                    {sup.supervisor_name}
                  </option>
                ))}
              </select>
            </div>

            {/*  () */}
            <img
              src={`http://localhost:5000/uploads/${student.profile_image || 'default-profile.png'}`}
              alt={student.student_name}
              className="w-24 h-24 object-cover rounded-full border-4 border-[#9AE5F2] shadow"
            />

            {/*  */}
            <div className="flex-1 space-y-1">
              <p className="text-lg font-bold">{student.student_name}</p>
              <p className="text-sm">: {student.student_id}</p>
              <p className="text-sm">: {student.age} | : {student.gender}</p>
              <p className="text-sm">: {student.phone_number}</p>
              <p className="text-sm">: {student.email}</p>
              <p className="text-sm">: {student.company_name}</p>
              <p className="text-sm">
                : {student.company_province?.trim() || ""}
              </p>

              {student.supervisor_name && (
                <p className="text-green-700 font-semibold mt-2">
                   : {student.supervisor_name}
                </p>
              )}
            </div>
          </div>

            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default DashboardInstructorAssign;
